.DateRangePicker {
}

/** CONTAINER STYLES **/

.DateRangePickerInput {
  background-color: transparent;
  border-width: 0;
  display: flex;
}

.DateRangePicker_picker {
  margin-top: -0.15rem;
}

/** INPUT DIV STYLES **/

.DateInput {
  background: transparent;
  display: flex;
  flex: 1 1 0%;
  width: auto;
}

/** INPUT FIELD COMMON STYLES **/

.DateInput_input {
  /* used data:image as background-image because svg import with relative url didn't work */
  background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><g fill="%236b6b6b" fill-rule="evenodd"><path d="M20 20h-4v-4h4v4zm-6-10h-4v4h4v-4zm6 0h-4v4h4v-4zm-12 6h-4v4h4v-4zm6 0h-4v4h4v-4zm-6-6h-4v4h4v-4zm16-8v22h-24v-22h3v1c0 1.103.897 2 2 2s2-.897 2-2v-1h10v1c0 1.103.897 2 2 2s2-.897 2-2v-1h3zm-2 6h-20v14h20v-14zm-2-7c0-.552-.447-1-1-1s-1 .448-1 1v2c0 .552.447 1 1 1s1-.448 1-1v-2zm-14 2c0 .552-.447 1-1 1s-1-.448-1-1v-2c0-.552.447-1 1-1s1 .448 1 1v2z"/></g></svg>');
  background-size: 14px;
  background-position: 10px center;
  background-repeat: no-repeat;
}

.DateInput_input {
  cursor: pointer;
  --border-opacity: 1;
  border-color: #0944b3;
  border-color: rgba(9, 68, 179, var(--border-opacity));
  margin-top: 0.5rem;
  --bg-opacity: 1;
  background-color: #000;
  background-color: rgba(0, 0, 0, var(--bg-opacity));
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  transition-property: background-color, border-color, color, fill, stroke, opacity, box-shadow, transform;
  transition-duration: 300ms;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border-top-width: 1px;
  border-left-width: 1px;
  border-right-width: 1px;
  border-bottom-width: 1px;
  border-style: solid;
  border-radius: 0.25rem;
  width: 100%;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  padding-left: 0.75rem;
  padding-right: 0.75rem;
  font-size: 0.75rem;
  --text-opacity: 1;
  color: #fff;
  color: rgba(255, 255, 255, var(--text-opacity));
  line-height: 1.25;
  padding-left: 2rem;
}

.DateInput_input:hover {
  --border-opacity: 1;
  border-color: #a0aec0;
  border-color: rgba(160, 174, 192, var(--border-opacity));
}

.DateInput_input:focus {
  --border-opacity: 1;
  border-color: #a0aec0;
  border-color: rgba(160, 174, 192, var(--border-opacity));
  outline: 0;
}

/** FIRST INPUT STYLES **/

.DateInput:first-child .DateInput_input {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.DateInput:first-child .DateInput_input:hover,
.DateInput:first-child .DateInput_input:focus {
  position: relative;
  z-index: 10;
}

/** SECOND INPUT STYLES **/

.DateInput:last-child .DateInput_input {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  margin-left: -1px;
}

/** ARROW STYLES **/

.DateRangePickerInput_arrow {
  display: none;
}

/* SELECT MONTH PICKER */

.DateRangePicker_select {
  --bg-opacity: 1;
  background-color: #fff;
  background-color: rgba(255, 255, 255, var(--bg-opacity));
  --text-opacity: 1;
  color: #1f1f27;
  color: rgba(31, 31, 39, var(--text-opacity));
  border-width: 1px;
  border-radius: 0.25rem;
  --border-opacity: 1;
  border-color: #726f7e;
  border-color: rgba(114, 111, 126, var(--border-opacity));
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  cursor: pointer;
  font-size: 0.875rem;
  padding-left: 0.5rem;
  padding-right: 1.25rem;
  padding-top: 0.15rem;
  padding-bottom: 0.15rem; /* NEEDED FOR ARROW DOWN */
  background-image: linear-gradient(45deg, transparent 50%, gray 50%),
    linear-gradient(135deg, gray 50%, transparent 50%);
  background-position: calc(100% - 11px) 11px, calc(100% - 6px) calc(11px);
  background-size: 5px 5px, 5px 5px;
  background-repeat: no-repeat;
}

/* CALENDAR DAYS */

.CalendarDay {
  border-width: 0;
  border-radius: 9999px;
}

.CalendarDay:hover,
.CalendarDay__selected,
.CalendarDay__selected:active,
.CalendarDay__selected:hover {
  --bg-opacity: 1;
  background-color: #0944b3;
  background-color: rgba(9, 68, 179, var(--bg-opacity));
  --text-opacity: 1;
  color: #fff;
  color: rgba(255, 255, 255, var(--text-opacity));
  border-width: 0;
  --border-opacity: 1;
  border-color: #0944b3;
  border-color: rgba(9, 68, 179, var(--border-opacity));
}

.CalendarDay__blocked_out_of_range:hover {
  border-width: 0;
  --bg-opacity: 1;
  background-color: #fff;
  background-color: rgba(255, 255, 255, var(--bg-opacity));
  cursor: not-allowed;
  --text-opacity: 1;
  color: #726f7e;
  color: rgba(114, 111, 126, var(--text-opacity));
}

.CalendarDay__selected_span {
  border-width: 0;
  --bg-opacity: 1;
  background-color: #5acce6;
  background-color: rgba(90, 204, 230, var(--bg-opacity));
}

/* MONTH NAVIGATION BUTTONS */

.DayPickerNavigation_button__horizontalDefault,
.DayPickerNavigation_button__horizontalDefault:hover {
  --border-opacity: 1;
  border-color: #726f7e;
  border-color: rgba(114, 111, 126, var(--border-opacity));
  --text-opacity: 1;
  color: #726f7e;
  color: rgba(114, 111, 126, var(--text-opacity));
  top: 24px;
  padding: 3px 9px;
}

.DayPickerNavigation_svg__horizontal {
  fill: currentColor;
}
