/* TODO: Create tailwind styles for this component */

.sb-topLeft {
  top: 0;
  left: 0;
  bottom: auto;
  right: auto;
}

.sb-topCenter {
  transform: translateX(-50%);
  top: 0;
  bottom: auto;
  left: 50%;
}

.sb-topRight {
  right: 0;
  top: 0;
  left: auto;
  bottom: auto;
}

.sb-bottomLeft {
  right: auto;
  left: 0;
  bottom: 0;
  top: auto;
}

.sb-bottomCenter {
  top: auto;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.sb-bottomRight {
  margin: 10px 0 0;
  top: auto;
  bottom: 0;
  left: auto;
  right: 0;
}

.sb-topLeft .sb-item,
.sb-topCenter .sb-item,
.sb-topRight .sb-item {
  margin: 10px 0 0;
}

.sb-bottomLeft .sb-item,
.sb-bottomCenter .sb-item,
.sb-bottomRight .sb-item {
  margin: 0 0 10px;
}

.sb-closeBtn {
  text-shadow: none;
  width: 20px;
  height: 20px;
  right: 5px;
  top: 5px;
  overflow: hidden;
  opacity: 1;
  border-radius: 9999px;
  padding: 0.15rem;
  --bg-opacity: 1;
  background-color: #fff;
  background-color: rgba(255, 255, 255, var(--bg-opacity));
  cursor: pointer;
  position: absolute;
  text-align: center;
  transition-duration: 300ms;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.sb-closeBtn:hover {
  background: #fff;
}

.sb-closeIcon {
  width: 100%;
  position: relative;
  overflow: hidden;
  height: 100%;
  display: block;
  line-height: 1;
}

.sb-closeIcon:after,
.sb-closeIcon:before {
  content: ' ';
  height: 2px;
  width: 12px;
  transition-duration: 300ms;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  display: block;
  --bg-opacity: 1;
  background-color: #000;
  background-color: rgba(0, 0, 0, var(--bg-opacity));
  opacity: 1;
  position: absolute;
}

.sb-closeIcon:before {
  left: 4px;
  top: 3px;
  transform: rotate(45deg);
  transform-origin: 0px 50%;
}

.sb-closeIcon:after {
  right: 3px;
  top: 5px;
  transform: rotate(-45deg);
  transform-origin: calc(100% - 3px) 50%;
}

.sb-title {
  overflow-wrap: normal;
  word-break: normal;
  font-size: 1rem;
  font-weight: 700;
}

.sb-message {
  overflow-wrap: normal;
  word-break: normal;
  font-size: 0.875rem;
}

.sb-item {
  animation: fadein 1s;
  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.2), 0 1px 18px 0 rgba(0, 0, 0, 0.12),
    0 3px 5px -1px rgba(0, 0, 0, 0.14);
  position: relative;
  padding: 1.25rem;
  --text-opacity: 1;
  color: #fff;
  color: rgba(255, 255, 255, var(--text-opacity));
  overflow: hidden;
  border-radius: 0.375rem;
  transition-property: height;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 300ms;
}

@keyframes fadein {
  from {
    top: 30px;
    opacity: 0;
  }

  to {
    opacity: 1;
    top: 0;
  }
}

