.tooltip {
  position: absolute;
  z-index: 10;
}

/* TOOLTIP WORKAROUND FOR ARROW UP */

.tooltip.tooltip-bottom .tooltip-box::before {
  position: absolute;
  z-index: 10;
  --bg-opacity: 1;
  background-color: #090c29;
  background-color: rgba(9, 12, 41, var(--bg-opacity));
  content: '';
  width: 14px;
  height: 1px;
  top: -1px;
  left: 50%;
  transform: translateX(-50%);
}

.tooltip.tooltip-bottom {
  padding-top: 0.5rem;
  margin-top: 0.15rem;
  left: 50%;
  transform: translateX(-50%);
}

.tooltip.tooltip-bottom-left {
  padding-top: 0.5rem;
  margin-top: 0.15rem;
  left: 0;
}

.tooltip.tooltip-bottom-right {
  padding-top: 0.5rem;
  margin-top: 0.15rem;
  right: 0;
}

.tooltip.tooltip-left {
  margin-right: 1rem;
  top: 50%;
  right: calc(100%);
  transform: translateY(-50%);
}

.tooltip.tooltip-right {
  margin-left: 1rem;
  top: 50%;
  left: calc(100%);
  transform: translateY(-50%);
}

.tooltip.tooltip-right .tooltip-box::before {
  position: absolute;
  z-index: 10;
  --bg-opacity: 1;
  background-color: #090c29;
  background-color: rgba(9, 12, 41, var(--bg-opacity));
  content: '';
  width: 2px;
  height: 15px;
  left: -1px;
  top: 50%;
  transform: translateY(-50%);
}

.tooltip.tooltip-left .tooltip-box::before {
  position: absolute;
  z-index: 10;
  --bg-opacity: 1;
  background-color: #090c29;
  background-color: rgba(9, 12, 41, var(--bg-opacity));
  content: '';
  width: 2px;
  height: 15px;
  right: -1px;
  top: 50%;
  transform: translateY(-50%);
}

.tooltip.tooltip-bottom-right .tooltip-box::before {
  position: absolute;
  z-index: 10;
  --bg-opacity: 1;
  background-color: #090c29;
  background-color: rgba(9, 12, 41, var(--bg-opacity));
  content: '';
  width: 15px;
  height: 2px;
  right: 5px;
  top: -1px;
}

.tooltip.tooltip-bottom-left .tooltip-box::before {
  position: absolute;
  z-index: 10;
  --bg-opacity: 1;
  background-color: #090c29;
  background-color: rgba(9, 12, 41, var(--bg-opacity));
  content: '';
  width: 15px;
  height: 2px;
  left: 5px;
  top: -1px;
}
